:root {
  --fh-panel-bg: #07060b;
  --fh-panel-title: #737373;
  --fh-item-slot-bg: #1f1f21;
  --fh-item-bg: #444444;
  --fh-item-gap: 6px;
  color: whitesmoke;
}

* {
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

input {
  font-family: inherit; /* 1 */
  font-size: inherit; /* 1 */
  line-height: inherit; /* 1 */
  margin: 0; /* 2 */
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0px 0px 0px 0px;
}

h2 {
  margin: 0.5em 0px 0.5em 0px;
}

h3 {
  margin: 0.3em 0px 0.3em 0px;
}

select,
input,
button {
  margin: 0.3em 0px 0.3em 0px;
  background-color: #000a;
  color: whitesmoke;
}

p {
  margin: 0.1em 0px 0.2em 0px;
}

.info-panel {
  /* border: 2px solid steelblue; */
  margin: 0.2em 0px 0.2em 0px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.leaflet-container {
  width: 100vw;
  height: 100vh;
}

input[type="checkbox"] {
  margin: 5px;
}

.mapicon-controls {
  position: absolute;
  bottom: 0px;
  left: 0;
  width: 4em;
  background-color: #000a;
  z-index: 1001;
}

.mapicon-toggle-image {
  width: 2em;
  height: 2em;
}

.checkbox-row {
  display: flex;
  flex-direction: row;
}

.structure-panel {
  position: absolute;
  padding-left: 10px;
  top: 0;
  right: 0;
  width: 33%;
  height: 100vh;
  background-color: #000a;
  z-index: 1000;
}

.structure-icon {
  opacity: 0.8;
}

.icon-green {
  background-color: #516c4b;
}

.icon-blue {
  background-color: #245682;
}

.stockpile-view {
  display: flex;
  flex-direction: column;
  /* gap: 10px; */
}

.stockpile-list-controls {
  /* margin-top: 20px; */
}

.items-list {
  display: flex;
  gap: var(--fh-item-gap);
  padding: var(--fh-item-gap);
  flex-wrap: wrap;
  overflow-y: scroll;
  max-height: 65vh;
}

.items-filters {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.item-filter-icon {
  width: 32px;
  height: 32px;
}

.item-slot {
  background-color: var(--fh-item-slot-bg);
  width: 64px;
  height: 64px;
  flex-basis: none;
}

.item {
  position: relative;
  display: flex;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background-color: var(--fh-item-bg);
  /* font-size: smaller; */
  font-size: 1rem;
}

.item-icon {
  /* position: absolute; */
  margin: auto;
  width: 80%;
  height: 80%;
  z-index: 0;
}

.item-icon-disabled {
  opacity: 50%;
}

.item-label {
  color: whitesmoke;
  position: absolute;
  top: -4px;
  left: 2px;
  z-index: 1;
  font-size: 0.8em;
  /* -webkit-text-stroke: 1px #fff; */
  text-shadow: 1px 0 0 black, -1px 0 0 black, 0 1px 0 black, 0 -1px 0 black,
    1px 1px black, -1px -1px 0 black, 1px -1px 0 black, -1px 1px 0 black;
}

.item-amount {
  color: white;
  position: absolute;
  bottom: 0px;
  right: 0px;
  font-size: 0.9em;
  text-align: right;
  z-index: 1;
}

.item-amount-input {
  width: 100%;
  border: none;
  background: none;
  text-align: right;
  -webkit-appearance: none;
  -moz-appearance: textfield;
}

.team-filter-icon {
  width: 32px;
  height: 32px;
}

.grayscale {
  filter: grayscale(100%);
}

.filters-row {
  display: flex;
  flex-direction: row;
  gap: 40px;
}
